
#topbar{
	background: #fff;
	box-shadow: 0 1px 3px 0 rgba(0,0,0,.25);
	height: 64px;
}
#topbar>.wrapper{
	min-width: 1024px;
	max-width: 1440px;
	margin: 0 auto;
	height: 64px;
}
#topbar>.wrapper{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
#topbar .logo{
	font-size: 24px;
	color: #000;
}



